import { TableCell, TableRow } from '@mui/material';

export default function RowListEventReg({ event, user }) {
	console.log(event);
	return (
		<TableRow hover role="checkbox" tabIndex={-1}>
			<TableCell align="left">{event.name}</TableCell>
			<TableCell align="left">{event.location}</TableCell>
			<TableCell align="center">
				{' '}
				{new Date(event.dateEvent).toLocaleTimeString('vi-VN').split(':')[0] +
					':' +
					new Date(event.dateEvent).toLocaleTimeString('vi-VN').split(':')[1] +
					'\n' +
					new Date(event.dateEvent).toLocaleDateString('vi-VN')}
			</TableCell>
			<TableCell align="center">
				{event.typeEvent === 'public' && 'S kin cng khai'}
				{event.typeEvent === 'private' && 'S kin ring '}
				{event.typeEvent === 'restricted' && 'S kin hn ch'}
			</TableCell>
			<TableCell align="center">
				{Date.now() > new Date(event.dateEvent).getTime() && ' kt thc'}
				{Date.now() < new Date(event.dateEvent).getTime() && 'Sp din ra'}
				{Date.now() === new Date(event.dateEvent).getTime() && 'ang din ra'}
			</TableCell>
		</TableRow>
	);
}
